import uCharts from '../../lib/u-charts/u-charts.js'; 
import {
	getSwiper,
	getRing,
} from '../../service/api/home.js'; //首页api
import {
	getisLogin,
} from '../../service/api/login.js' //登陆api
var _self;
var canvaRing=null;
  
export default {
	data() {
		return {
			swiperList: [], //轮播数据
			//轮播
			cardCur: 0,
			dotStyle: true,
			towerStart: 0,
			direction: '',
			//统计数据
			ringList:{}, 
			//环状统计初始化数据
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			//总小时公里
			hour: 5,
			minute: 20,
			kilometer: 26,
			
		}
	},
	mounted() {
		_self = this;
		getisLogin() //是否登陆
		
		_self.getSwiperData();//获取轮播数据
		
		_self.cWidth=uni.upx2px(400);
		_self.cHeight=uni.upx2px(400);
		_self.getServerData();//获取统计数据
		
	},
	methods: {
		getSwiperData(){
			var _this=this;
			//获取轮播数据
			getSwiper()
			.then(res => {
				//console.log(res)
				_this.swiperList=res.data.swiperList
			}).catch(err => {
			
			})
		},
		copyUrl() {
			uni.setClipboardData({
				data: 'https://github.com/AmosHuKe/Watch-Test',
				success: function () {
					uni.showToast({
						title: '地址复制成功'
					})
				}
			});
		},
		getServerData(){
			var _this=this;
			//获取统计数据
			getRing()
			.then(res => {
				//console.log(res)
				let data=res.data.series; //获取统计数据
				_this.ringList=data; //赋值统计数据
				var ringData={series:[]} //统计图数据装载
				for(let i=0;i<data.length;i++){
					//统计图数据装载
					ringData.series.push({"name":data[i].name,"data":data[i].data})
				}
				//console.log(ringData)
				_self.showRing("canvasRing",ringData);
			}).catch(err => {
				
			})
			
		},
		showRing(canvasId,chartData){
			canvaRing=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'ring',
				fontSize:11,
				legend:false, //底部tag
				// title: {
				// 	name: '',
				// 	color: '#7cb5ec',
				// 	fontSize: 25*_self.pixelRatio,
				// 	offsetY:-20*_self.pixelRatio,
				// },
				subtitle: {
					name: '',
					color: '#666666',
					fontSize: 1*_self.pixelRatio,
					offsetY:2*_self.pixelRatio,
				},
				extra: {
					pie: {
					  offsetAngle: -45,
					  ringWidth: 5*_self.pixelRatio,
					  lableWidth: 15,
					}
				},
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				disablePieStroke: false,
				dataLabel: true,
			});
		},
		touchRing(e){
			canvaRing.showToolTip(e, {
				format: function (item) {
					return item.name + ':' + item.data 
				}
			});
		},
		DotStyle(e) {
			this.dotStyle = e.detail.value
		},
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		// towerSwiper
		// 初始化towerSwiper
		TowerSwiper(name) {
			let list = this[name];
			for (let i = 0; i < list.length; i++) {
				list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
				list[i].mLeft = i - parseInt(list.length / 2)
			}
			this.swiperList = list
		},
		
		// towerSwiper触摸开始
		TowerStart(e) {
			this.towerStart = e.touches[0].pageX
		},
		
		// towerSwiper计算方向
		TowerMove(e) {
			this.direction = e.touches[0].pageX - this.towerStart > 0 ? 'right' : 'left'
		},
		
		// towerSwiper计算滚动
		TowerEnd(e) {
			let direction = this.direction;
			let list = this.swiperList;
			if (direction == 'right') {
				let mLeft = list[0].mLeft;
				let zIndex = list[0].zIndex;
				for (let i = 1; i < this.swiperList.length; i++) {
					this.swiperList[i - 1].mLeft = this.swiperList[i].mLeft
					this.swiperList[i - 1].zIndex = this.swiperList[i].zIndex
				}
				this.swiperList[list.length - 1].mLeft = mLeft;
				this.swiperList[list.length - 1].zIndex = zIndex;
			} else {
				let mLeft = list[list.length - 1].mLeft;
				let zIndex = list[list.length - 1].zIndex;
				for (let i = this.swiperList.length - 1; i > 0; i--) {
					this.swiperList[i].mLeft = this.swiperList[i - 1].mLeft
					this.swiperList[i].zIndex = this.swiperList[i - 1].zIndex
				}
				this.swiperList[0].mLeft = mLeft;
				this.swiperList[0].zIndex = zIndex;
			}
			this.direction = ""
			this.swiperList = this.swiperList
		},
	}
}
